*{ 
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
  background: #eee;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card{
  align-items: center;
  border: 2px gray solid;
  border-radius: 6px;
  background: #aaa;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 10px;
  padding: 10px;
  overflow: hidden;
}

.card img{
  max-width: 400px;
}

.cardGrid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border: 2px solid gray;
  background: #ccc;
}